import './App.css';
import { useState } from 'react';
import data from "./data"

const App = () => { 
  
  const initialState = {
    lastNum: 0,
    formula: 0,
    operator: 0,
  }
  
  const [state, setState] = useState(initialState)
  
  const handleClick = e => {
    const thisNum = e.target.innerText
    const { lastNum, formula, operator, output } = state
    console.log(thisNum, lastNum, formula)
    
    if (thisNum == "CLEAR") {
      setState(initialState)
    } else if (thisNum == "=") {
      const operandArray = formula.match(/[^0-9]{2,}/g)
      console.log(operandArray)
      if (operandArray == null) {
          setState({
          formula: eval(formula)
        })
      } else {
          const operators = operandArray.map(str => str.split(''))
          console.log(operandArray, operators)
          const newOperators = []
          operators.map(array => {
            if (array[array.length-1] == '-') {
              newOperators.push(array[array.length-2].concat(array[array.length-1]))
            } else {
              newOperators.push(array[array.length-1])
            }
          })
          console.log(newOperators)
          const result = formula.replace(/[^0-9]{2,}/g, " ")
          const resultArray = result.split(' ')
          console.log(result, resultArray)
          let final = []

          for (let i = 0; i < newOperators.length; i++) {
            final.push(resultArray[i].concat(newOperators[i]))
          }
          final.push(resultArray[resultArray.length-1])
          const finalStr = final.join()
          const answer = finalStr.replace(/,+/g, "")
          console.log(finalStr, answer)

           // console.log(eval(formula))
           setState({
             formula: eval(answer)
           })
      }
    } else if (thisNum == ".") {
        const bool = formula.search(/[\+-/*]+/g) > 0
        if (bool) {
          const item = formula.split("").reverse().join("").match(/[\+-/*]+/g)
          lastNum != '.' && item[0] != '.' && setState({
                                                lastNum: thisNum,
                                                formula: formula + thisNum
                                              })
        } else {
          lastNum != '.' && setState({
                              lastNum: thisNum,
                              formula: formula + thisNum
                            })
        }
     } else if (thisNum == "/" || thisNum == "*" || thisNum == "+" || thisNum == "-") {
              setState({
              lastNum: thisNum,
              formula: formula == 0 ? 
              0 : 
              formula + thisNum
            })
           // }
       } 
    else {
       setState({
       lastNum: thisNum,
       formula: formula == 0 ? thisNum : formula + thisNum
      })
    } 
  }

  const buttons = data.map(symbol => <Button value={symbol.key}
                                             name={symbol.id}
                                             handleClick={handleClick} />)           
  return (
    <div id="wrapper">
      <div id="calculator">
        <p className="screen" id="display">{state.formula}</p>
        <div className="buttons">
          {buttons}
        </div>
      </div>
    </div>
  )
}

const Button = (props) => {
  const value = props.value
  const name = props.name
  
  return (
    <div 
      id={name}
      className='button'
      onClick={props.handleClick}>{value}</div>
  )
}

export default App